.auth__form {
  display: flex;
  flex-direction: column;
  margin: 50px auto 0;
  padding: 0;
  max-width: 358px;
}

.auth__form input {
  display: block;
  width: 358px;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid #cccccc;
  margin: 0;
  padding: 0 0 13px;
  color: #ccc;
  font-family: 'Inter', Arial, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}

.auth__form input:focus {
  outline: none;
  border-bottom: 2px solid rgb(60, 97, 219);
}

.auth__form input::-webkit-input-placeholder {
  color: #ccc;
}
.auth__form input::-moz-placeholder {
  color: #ccc;
}
.auth__form input:-ms-input-placeholder {
  color: #ccc;
}

.auth__form button {
  font-family: 'Inter', Arial, sans-serif;
  width: 358px;
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  align-content: center;
  justify-content: center;
  font-size: 18px;
  line-height: 22px;
  border: none;
  border-radius: 2px;
  transition: 0.8s ease;
  margin: 176px 0 0;
  padding: 0;
}

.auth__form button:hover {
  opacity: 0.85;
  cursor: pointer;
}
