.profile__edit-avatar-button {
  background: #000000 url(../../../images/edit-avatar-button.svg) no-repeat center;
  width: 120px;
  height: 120px;
  border: none;
  border-radius: 100%;
  position: absolute;
  padding: 0;
  opacity: 0;
}


.profile__edit-avatar-button:hover {
  transition: opacity 0.3s linear;
  opacity: 0.8;
  cursor: pointer;
}

@media (max-width: 768px) {
  .profile__edit-avatar-button {
    margin-bottom: 26px;
  }
}

@media (max-width: 620px) {
  .profile__edit-avatar-button {
    margin-right: 0;
  }
}
